.header-container{
  background-color:rgba(3,37,65,1)
}
.header-wrap{
  height:64px;
  max-width:1400px;
  margin:0 auto;

  display:flex;
  align-items:center;
}
.header-wrap ul{
  display:flex;
  margin-left:20px;
}
.header-wrap ul li{
  margin-right:14px;
}
.header-wrap ul li a{
  color:#fff;
  text-decoration:none;
  font-weight:600;
  padding:8px;
}

.page-container{
  max-width:1400px;
  margin:0 auto;
  padding:32px 0;
}

.app-container{
  max-width:1400px;
  margin:0 auto;
  display:flex;
  flex-wrap:wrap;
  justify-content:center;
}
.movie-container{
  width:250px;
  margin:16px;
  background-color:#373b69;
  color:#fff;
  border-radius:5px;
  box-shadow:3px 3px 5px rgba(0,0,0,0.1)
}
.movie-container p{
  margin:0
}
.movie-container img{
  max-width:100%;
  height:375px;
}
.movie-info{
  display:flex;
  padding:20px;
  justify-content:space-between;
  align-items:center;
}
.movie-info h4{
  margin:0;
  word-break:keep-all
}
.movie-info span{
  margin-left:3px
}